import Vue from 'vue';
import Router from 'vue-router';
import index from '@/view/index.vue';

Vue.use(Router);

export default new Router({
	routes: [
		{
			path: '*',
			redirect: '/index'
		},
		{
			path: '/index',
			name: 'index',
			component: index
			// children: [
			//   {
			//     path: '/detail',
			//     name: 'detail',
			//     component: detail
			//   },
			//   {
			//     path: '/show',
			//     name: 'show',
			//     component: show
			//   }
			// ]
		}
	]
});



