<template>
  <div>
    <p>{{data}}</p>
    <p>{{data1}}</p>
    <button @click="get">get</button>
    <button @click="post">post</button>
  </div>
</template>

<script>
import servers from '../api/api';
// import http from 'axios';

export default {
  name: 'HelloWorld',
  components: {

  },
  data() {
    return {
      data: 123,
      data1: 123,
    };
  },
  mounted() {

  },
  methods: {
    get() {
      servers.get({ time: 1 }, resp => {
        this.data = resp;
      });
    },
    post() {
      servers.post({ time: 1 }, resp => {
        this.data1 = resp;
      });
    }
  }
};
</script>

<style scoped>
</style>



